import React, { PureComponent } from 'react'
import { Modal, Row, Col, Form, Button, message, Input } from 'antd'
import { CsxFormSelect } from 'Form'
import {
  API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { isArray } from 'util'
const FormItem = Form.Item
@connect(
  state => ({ ...state.sysFactoryLocationConfig, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getSingleDet, getLocationlList } = this.props
    if (modalData) {
      //编辑
      const { id = '' } = modalData
      const params = {
        urlParams: [`${id}`]
      }
      id && getSingleDet(params, data => {})
    }
    getLocationlList()
  }
  handleSubmit = e => {
    const { form, handleAddOrEdit, workType, modalData } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      if (!err) {
        const { isFactory = false, id = '' } = modalData
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'dicTypeObj') {
              reqObj.memo = reqObj[i].children
              reqObj.dicType = reqObj[i].value
            }
          }
        }
        if (isFactory) {
          reqObj.dicKey = reqObj && reqObj.dicKey && reqObj.dicKey.value.split(' ')[0]
        }
        let result = {
            data: {
              ...reqObj,
              id
            }

          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (workType && workType === 'edit') {
          result.data.workType = 'edit'
          msg = '编辑成功'
        }
        delete result.dicTypeObj
        delete result.location
        delete result.locationList
        console.log('result is', result)
        handleAddOrEdit({ ...result }, data => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  render() {
    const {
      update,
      modalTitle,
      addOneItemSelf,
      locationList,
      form: { getFieldDecorator, setFieldsValue, getFieldsValue },
      modalTuttonLoading,
      modalData = {}
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    let copyList = []
    if (locationList) {
      copyList = JSON.parse(JSON.stringify(locationList))
    }
    if (addOneItemSelf) {
      copyList && copyList.unshift(addOneItemSelf)
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row>
            <Col span={20}>
              <FormItem {...formItemLayout} label='类别' hasFeedback>
                {getFieldDecorator('dicTypeObj', {
                  initialValue: modalData.dicTypeObj,
                  rules: [
                    {
                      required: true,
                      message: '请选择!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    dateApi={API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE}
                    // label='类别'
                    selectItem={{
                      key: 'code',
                      valueKey: ['name']
                    }}
                    handleSelectChange={data => {
                      const { value = '' } = data
                      const { dicType, dicKey, dicValue } = getFieldsValue()
                      if (value != (modalData.dicType) && (value != dicType)) {
                        update({
                          modalData: {
                            ...modalData,
                            isFactory: value == '10010' ? true : false // 工厂地点
                          }
                        })
                        setFieldsValue({
                          dicKey: '',
                          dicValue: ''
                        })
                      } else {
                        update({
                          modalData: {
                            ...modalData,
                            dicKey,
                            dicValue,
                            dicType: value,
                            isFactory: value == '10010' ? true : false // 工厂地点
                          }
                        })
                      }
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          {
            modalData && modalData.isFactory ?
              <div>
                <Row gutter={16}>
                  <Col span={20}>
                    <FormItem {...formItemLayout} label={'编码'} hasFeedback>
                      {getFieldDecorator('dicKey', {
                        initialValue: modalData.dicKey,
                        rules: [
                          {
                            required: true,
                            message: '请选择!'
                          }
                        ]
                      })(
                        <CsxFormSelect
                          data={ copyList}
                          addOneItemSelf={addOneItemSelf}
                          selectItem={{
                            key: 'codeAndName',
                            valueKey: ['code']
                          }}
                          handleSelectChange={data => {
                            console.log('data is', data)
                            const dataName = data && data.value ? data.value.split(' ')[1] : ''
                            setFieldsValue({
                              dicValue: dataName
                            })
                          }}
                        />
                      )}
                    </FormItem>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col span={20}>
                    <FormItem {...formItemLayout} label={'名称'} hasFeedback>
                      {getFieldDecorator('dicValue', {
                        initialValue: modalData.dicValue,
                        rules: [
                          {
                            required: true,
                            message: '请输入'
                          }
                        ]
                      })(<Input disabled />)}
                    </FormItem>
                  </Col>
                </Row>

              </div>
              : <div>
                <Row gutter={16}>
                  <Col span={20}>
                    <FormItem {...formItemLayout} label='编码' hasFeedback>
                      {getFieldDecorator('dicKey', {
                        initialValue: modalData.dicKey,
                        rules: [
                          {
                            required: true,
                            message: '请输入'
                          }
                        ]
                      })(<Input />)}
                    </FormItem>
                  </Col>

                </Row>
                <Row gutter={16}>
                  <Col span={20}>
                    <FormItem {...formItemLayout} label='名称' hasFeedback>
                      {getFieldDecorator('dicValue', {
                        initialValue: modalData.dicValue,
                        rules: [
                          {
                            required: true,
                            message: '请输入'
                          }
                        ]
                      })(<Input />)}
                    </FormItem>
                  </Col>
                </Row>
              </div>
          }

           <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



