import React, { PureComponent } from 'react'
import { Form, Button, Row, Col } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE
} from 'api'
@connect(
  state => ({ ...state.sysFactoryLocationConfig }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentDidMount() {
    // 数据初始化
    // const { modalData, getSingleDet } = this.props
    // if (modalData) {
    //   //编辑
    //   const { locationCode = '' } = modalData
    //   const params = {
    //     urlParams: [`${locationCode}`]
    //   }
    //   getSingleDet(params, data => {})
    // }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {}, productCodeOptions: {} })
  }
  render() {
    const {
      searchParams = {}
    } = this.props
    const { dicType = '' } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
        <Col span={8}>
            <CsxSelect
              dateApi={API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE}
              label='类别'
              allowClear={true}
              selectItem={{
                data: dicType,
                key: 'code',
                valueKey: ['name']
              }}
              onChangeSelect={data => {
                this.onInputChange({ dicType: data })
              }}
            />
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



