import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_SYSFACTORYLOCATIONCONFIG_PAGE,
  API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE,
  API_SYSFACTORYLOCATIONCONFIG_DEL,
  API_SYSFACTORYLOCATIONCONFIG_LIST_FACTORY,
  API_SYSFACTORYLOCATIONCONFIG_CREATE,
  API_SYSFACTORYLOCATIONCONFIG_UPDATE,
  API_SYSFACTORYLOCATIONCONFIG_EXPORT,
  API_SYSFACTORYLOCATIONCONFIG_DETAIL
} from 'api'
//同步
export const update = object => ({
  type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
  data: object
})

// 获取 - 列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_PAGE, ...reqData })
  // getData({ api: API_SYSFACTORYLOCATIONCONFIG_PAGE1, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch(err => {
      console.log(err)
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 获取 - 类别列表
export const getTypelList = (reqData, callBack) => dispatch => {
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_LIST_TYPE, ...reqData })
    .then(data => {
      callBack && callBack()
      const result = data && data.result
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: {
          typeList: result
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 获取 - 地点列表
export const getLocationlList = (reqData, callBack) => dispatch => {
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_LIST_FACTORY, ...reqData })
    .then(data => {
      callBack && callBack()
      const result = data && data.result || []
      result && result.length && result.map((item) => item.codeAndName = `${item.code} ${item.name}`)
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: {
          locationList: result
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 删除 - 一键调拨数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_DEL, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 获取 - 单个基础配置数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        dicType = '',
        dicKey = '',
        memo = '',
        itemsList = []
      } = result
      let locationList = [],
        isFactory = dicType == '10010' ? true : false
      itemsList && itemsList.length && itemsList.map((item, index) => {
        const { transferLocationCode = '' } = item
        locationList.push(transferLocationCode)
      })
      console.log('locationList is', locationList)

      let dataFormater = {
        ...result,
        dicTypeObj: {
          value: `${dicType}`,
          children: `${memo}`
        },
        isFactory: dicType == '10010' ? true : false
      }
      if (isFactory) {
        dataFormater.dicKey = {
          value: `${dicKey} ${memo}`,
          children: `${dicKey}`
        }
      }
      console.log('dataFormater is', dataFormater)
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: {
          modalData: dataFormater,
          dataLocationList: itemsList
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 创建/编辑 - 一键调拨数据
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.workType && reqData.data.workType == 'edit'
      ? API_SYSFACTORYLOCATIONCONFIG_UPDATE
      : API_SYSFACTORYLOCATIONCONFIG_CREATE
  console.log('reqData is', reqData)
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
    data: { modalTuttonLoading_handleExport: true }
  })
  getData({ api: API_SYSFACTORYLOCATIONCONFIG_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.SYSFACTORYLOCATIONCONFIG_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
}



