import React, { PureComponent } from 'react'
import { Modal, message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.sysFactoryLocationConfig }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { update } = this.props
    update({ isShowModal: true, modalTitle: '新增', modalData: { isFactory: false, dicTypeObj: '' }, workType: 'add', addOneItemSelf: false })
  }
  // 删除
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys = [],
      update
    } = this.props
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      confirm({
        title: '温馨提示',
        content: '是否进行删除操作？',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleOperator(
            {
              data: selectedRowKeys
            },
            () => {
              update({ selectedRowKeys: [] })
              message.success('操作成功')
              handleGetList({ searchParams: { page, size, ...searchParams } })
            }
          )
        }
      })
      return true
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    const { menuPermissionIds = [], typeList = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds &&
          menuPermissionIds.includes('sysFactoryLocationConfig_add') && (
            <Button
              disabled={!(typeList && typeList.length > 0)}
              type='primary'
              className={style.btn}
              onClick={this.handleAdd}
            >
              新增
            </Button>
        )}
        {menuPermissionIds &&
          menuPermissionIds.includes('sysFactoryLocationConfig_delete') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleOperator}
            >
              删除
            </Button>
          )}
      </div>
    )
  }
}



