import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import OperationModal from './components/./OperationModal'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.sysFactoryLocationConfig,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: ''
    })
  }
  componentDidMount() {
    this.props.getTypelList() // 获取未配置调拨地点的工厂的列表一键调拨地点的列表
  }
  render() {
    const {
      menuPermissionIds
     } = this.props
    return (
      <section>
        <Bread route={['系统中心', '配置管理', '基础信息配置']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Tool menuPermissionIds={menuPermissionIds}/>
        <Table menuPermissionIds={menuPermissionIds}/>
        {this.props.isShowModal ? <OperationModal /> : null}
      </section>
    )
  }
}



